.carosel-div {
  position: relative;
  bottom: 300px;
  left: 50px;
  font-size: 30px;
  color: white;
  width: 500px;
}
.carosel-btn {
  font-size: 20px;
  cursor: pointer;
  padding: 20px 28px;
  color: white;
  border-radius: 10px;
  border: 1px solid rgb(43, 142, 208);
  background-color: rgb(43, 142, 208);
}
.carosel-para {
  font-size: 50px;
  text-align: left;
  margin-bottom: 20px;
}
.maincarosel {
  position: absolute;
  top: 0px;
}
.link-btn {
  color: aliceblue;
}
